interface I_State {
    navbar_selection: "" | "element" | "text" | "image" | "background" | "logo"
}

export const initialState = {
    navbar_selection: ""
}

export const designToolReducer = (InitialState: I_State, action) => {
    if (action.type == 'Navbar_Select_Elements') {
        return InitialState.navbar_selection = "element"
    }
    if (action.type == 'Navbar_Select_Text') {
        return InitialState.navbar_selection = "text"
    }
    if (action.type == 'Navbar_Select_Image') {
        return InitialState.navbar_selection = "image"
    }
    if (action.type == 'Navbar_Select_Background') {
        return InitialState.navbar_selection = "background"
    }
    if (action.type == 'Navbar_Select_Logo') {
        return InitialState.navbar_selection = "logo"
    }
}